:root { 
  --color1: #A2A182;
  --color2: #687259;
  --color3: #F4ECE2;
  --color4: #BA6F4D;
  --color5: #8E412E;
  --color6: #E6CEBC;
}

body {
  margin: 0;
  font-family: 'Raleway', sans-serif;
  /* font-family: 'Roboto Slab', serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: var(--color4);
  background-color: var(--color3);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navActive {
  color: var(--color2);
  font-weight: bold;
}

.header {
  text-align: center;
  padding-top: 4%;
  padding-bottom: 4%;
  color: var(--color5);
  background-color: var(--color1);
}

.header h1 {
  font-size: 35px;
}

.header ul {
  list-style: none;
  display: flex;
  justify-content: space-evenly;
  margin-right: 10%;
  font-family: 'Roboto Slab', serif;
  font-size: 18px;
}

.header ul li {
  cursor: pointer;
}

.about {
  border-left: solid var(--color2);
  margin: 0 8% 0 8%;
  padding-left: 4%;
}

h2 {
  color: var(--color2);
  font-size: 25px;
}

.about img {
  max-width: 60%;
  border-radius: 65px;
}

.about p {
  font-family: 'Roboto Slab', serif;

}

.portfolio {
  text-align: center;
}

.portfolio .container {
  position: relative;
  text-align: center;
  max-width: 70%;
  margin: auto;
}

.portfolio img { 
  max-width: 100%;
  margin-bottom: 4%;
}

.container:hover img {
  opacity: 40%;
}

.container:hover .container-text {
  opacity: 100%;
}

.portfolio .container-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  font-weight: bold;
  font-size: 22px;
  cursor: pointer;
}

.portfolio .container-text a {
  color: var(--color5);
  text-decoration: none;
  margin-right: 2%;
}

.contact {
  text-align: center;
}

.contact a {
  text-decoration: none;
}

.contact div {
  margin: 15px 0 30px 0;
  display: inline;
}

.contact p {
  color: var(--color4);
  font-size: 17px;
  padding-bottom: 17px;
}

.contact i {
  color: var(--color5);
  font-size: 30px;
}

.resume {
  text-align: center;
}

.resume a {
  text-decoration: none;
  color: var(--color4);
  font-family: 'Roboto Slab', serif;
}

.resume a span {
  font-weight: bold;
  cursor: pointer;
}

.resume h3 {
  font-size: 25px;
  text-decoration:overline;
}

.resume ul {
  text-align: start;
  list-style: disc;
  margin-left: 50px;
  font-family: 'Roboto Slab', serif;
}

.resume li {
  padding: 3px;
}

.page-container {
  position: relative;
}

.content-wrap {
  padding-bottom: 2.5rem;
}

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 2.5rem;
}

.footer ul {
  list-style: none;
  display: flex;
  justify-content: center;
  margin-right: 35px;
}

.footer li {
  padding: 15px;
}

.footer li a {
  color: var(--color5);
  font-size: 20px;
  cursor: pointer;
}

@media only screen and (min-width: 768px) {
  .header h1 {
    font-size: 60px;
  }

  .header ul {
    font-size: 30px;
    margin-left: 25px;
  }

  h2 {
    font-size: 40px;
  }

  .about p {
    font-size: 22px;
  }

  .portfolio .container-text {
    font-size: 40px;
  }

  .contact i {
    font-size: 50px;
  }
  
  .contact p {
    font-size: 25px;
  }

  .resume a {
    font-size: 25px;
  }

  .resume h3 {
    font-size: 35px;
  }

  .resume ul {
    margin-left: 25%;
    font-size: 25px;
  }

  .footer li a {
    font-size: 40px;
    padding: 20px;
  }
}

@media only screen and (min-width: 980px) {
  .header {
    font-size: 75px;
    margin: 0;
    padding: 2px;
  }

  .header ul {
    font-size: 25px;
    margin-left: 300px;
    margin-right: 340px;
  }

  .about-container {
    display: flex;
    justify-content: space-between;
  }

  .about img {
    max-width: 25%;
  }

  .about p {
    margin-left: 40px;
  }

  .portfolio .row {
    display: flex;
    flex-wrap: wrap;
    padding-left: 30px;
  }

  .portfolio .container {
    max-width: 27%;
    margin: 40px;
    flex: 50%;
  }

  .portfolio .container-text {
    font-size: 30px;
  }

  .error-text {
    font-size: 25px;
  }

  .contact {
    text-align: start;
  }

  .contact header {
    text-align: center;
  }

  .contact .container {
    display: flex;
    justify-content: space-around;
    margin: 30px 250px 0 250px;
  }

  .contact i {
    font-size: 65px;
  }

  .contact p {
    font-size: 25px;
  }

  .contact div {
    text-align: center;
  }

  .phone-contact {
    padding: 0 30px 0 30px;
  }

  .github-contact {
    padding: 0 75px 0 75px;
  }

  .linkedin-contact {
    padding: 0 75px 0 75px;
  }

  .contact a:hover {
    text-decoration: underline;
    text-decoration-color: var(--color4);
  }

  .resume h3 {
    font-size: 40px;
  }

  .resume ul {
    margin-left: 500px;
  }
}